import 'normalize.css';
import './mq.genie';
import '../scss/main.scss';

const scriptText = document.getElementById('script-text');
const copyButton = document.getElementById('copy-btn');
const mirrorsList = document.querySelectorAll('li[data-mirror-info]');
const mobileMenu = document.getElementById('mobile-menu');
const burgerMenu = document.getElementById('burger-menu');
const overlay = document.getElementById('overlay');

// 汉堡菜单
let menuOpen = false;
let menuStep = 1;
let menuTimer;
const menuItems = mobileMenu.querySelectorAll('.menu-items li a');

const closeMobileMenu = () => {
    clearTimeout(menuTimer);
    mobileMenu.className = 'mobile-menu';
    burgerMenu.className = 'burger-menu step-2 step-2-3';
    overlay.className = 'overlay';
    menuTimer = setTimeout(() => {
        burgerMenu.className = 'burger-menu';
    }, 150);
}
const openMobileMenu = () => {
    clearTimeout(menuTimer);
    mobileMenu.className = 'mobile-menu opened';
    burgerMenu.className = 'burger-menu step-2';
    overlay.className = 'overlay active';
    menuTimer = setTimeout(() => {
        burgerMenu.className = 'burger-menu step-3 step-2-3';
    }, 150);
}
burgerMenu.addEventListener('click', () => {
    if (menuOpen) {
        closeMobileMenu();
    } else {
        openMobileMenu();
    }
    menuOpen = !menuOpen;
});
for (let i = 0; i < menuItems.length; i++) {
    menuItems[i].addEventListener('click', () => {
        closeMobileMenu();
        menuOpen = false;
    });
}
overlay.addEventListener('click', () => {
    closeMobileMenu();
    menuOpen = false;
});

const featureLinks = Array.from(document.querySelectorAll('a[href="#features"]'));
featureLinks.map((e) => {
    e.addEventListener('click', (event) => {
        const mobileMenuStatus = getComputedStyle(mobileMenu).display !== 'none';
        window.scroll({
            top: document.getElementById('features').offsetTop - (mobileMenuStatus ? 64 : 0),
            behavior: "smooth"
        });
        event.preventDefault();
    });
    return e;
});

// 命令复制粘贴
if (ClipboardJS.isSupported()) {
    scriptText.classList.add('copyable');
    copyButton.style.display = 'block';
    const clipboard = new ClipboardJS(copyButton);
    clipboard.on('success', (e) => {
        console.info('Action:', e.action);
        console.info('Text:', e.text);
        console.info('Trigger:', e.trigger);
        e.clearSelection();
    });
}

// 节点切换
for (let i = 0; i < mirrorsList.length; i++) {
    mirrorsList[i].childNodes[0].addEventListener('click', (e) => {
        scriptText.value = mirrorsList[i].dataset.mirrorInfo;
        for (let j = 0; j < mirrorsList.length; j++) {
            if (i === j) {
                mirrorsList[j].className = 'current';
            } else {
                mirrorsList[j].className = '';
            }
        }
        e.preventDefault();
    });
}

// Star 与 Fork
const request = new XMLHttpRequest();
request.open('GET', 'https://api.github.com/repos/SilverBlogTeam/SilverBlog', true);
request.onload = () => {
    if (request.status >= 200 && request.status < 400) {
        const data = JSON.parse(request.responseText);
        const starDOM = document.getElementById('github-star-count');
        starDOM.style.display = 'block';
        starDOM.textContent = data.stargazers_count.toLocaleString();
        const forkDOM = document.getElementById('github-fork-count');
        forkDOM.style.display = 'block';
        forkDOM.textContent = data.forks_count.toLocaleString();
    } else {
        console.error(`Server returned ${request.status}`);
    }
};
request.onerror = (e) => {
    console.error(e);
};
request.send();

